@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  background: #003840;
}
.container {
  display: grid;
  grid-template-columns: repeat(4, minmax(60px,80px));
  grid-template-rows: 20px 30px 30px repeat(5, 70px);
  grid-gap: 5px;
  border-radius: 5px;
  background: #005a5b;
  padding: 10px;
  padding-top: 5px;
  
}
#formula {
  grid-column: 1 / span 4;
  grid-row: 2 / span 2;
  text-align: right;
  border-radius: 5px;
  background: #02a676;
  padding-right: 5px;
  padding-top: 5px;
  font-weight: bold;
  overflow: auto;
}
#display {
  grid-row: 3;
  grid-column: 1 / span 4;
  text-align: right;
  padding-right: 6px;
  font-size: 22px;
  font-weight: bold;
}
[id="AC"] {
  grid-column: span 2;
}
[id="0"] {
  grid-column: span 2;
}
[id="÷"], [id="×"], [id="-"]{
  font-size: 24px;
}
.btn{
  font-family: 'Montserrat', sans-serif;
  background: #005a5b ;
  color: #02a676;
  font-size: 22px;
  font-weight: bold;
  border: 1px solid #008c72;
  border-radius: 5px;
}
.btn:active{
  background: #008c72;
  padding-right: 0;   
}
.about{
  grid-row: 1;
  grid-column: 1 / span 4;
  font-size: 10px;
  font-weight: bold;
  color: #02a676;
  text-align: center;
}
.about a{
  text-decoration: none;
  color: #02a676;
}
.about a:hover{
  text-decoration: underline;
}

